import React, {Component} from 'react';
import NumberFormat from 'react-number-format';
import ItemProduct from '../ItemProduct';
class Index extends Component {

    showDataRelated = () => {
        return this.props.data.dataRelated.map((item,key) => {
            return (
                <div key={key} className="col-lg-3 col-md-4 col-sm-6">
                    <ItemProduct data={item}/>
                </div>
            );
        })
    }

    isOpenDataRelated = () => {
        if (this.props.data.dataRelated.length > 0) {
            return (
                <section className="related-product">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="section-title related__product__title">
                                    <h2>Sản phẩm khác</h2>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            {this.showDataRelated()}
                        </div>
                    </div>
                </section>
            );
        }
    }

    render() {
        const {data} = this.props.data;
        const dataDetail = data;
        return (
            <div>
                <section className="product-details spad">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-6 col-md-6">
                                <div className="product__details__pic">
                                    <div className="product__details__pic__item">
                                        <img className="product__details__pic__item--large" src={dataDetail.icon} alt={dataDetail.name} />
                                    </div>
                                    <div className="product__details__pic__slider owl-carousel">
                                        <img data-imgbigurl={dataDetail.icon} src={dataDetail.icon} alt={dataDetail.name} />
                                    </div>
                                </div>
                            </div>
                            <div className="col-lg-6 col-md-6">
                                <div className="product__details__text">
                                    <h3>{dataDetail.name}</h3>
                                    <div className="product__details__rating">
                                        <i className="fa fa-star" />
                                        <i className="fa fa-star" />
                                        <i className="fa fa-star" />
                                        <i className="fa fa-star" />
                                        <i className="fa fa-star-half-o" />
                                        <span>(18 reviews)</span>
                                    </div>
                                    <div className="product__details__price"><NumberFormat value={dataDetail.giaban} displayType={'text'} thousandSeparator={true} suffix={' đ'} /></div>
                                    <div dangerouslySetInnerHTML={{ __html:dataDetail.intro }} />
                                    {/*<div className="product__details__quantity">*/}
                                        {/*<div className="quantity">*/}
                                            {/*<div className="pro-qty">*/}
                                                {/*<input type="text" defaultValue={1} />*/}
                                            {/*</div>*/}
                                        {/*</div>*/}
                                    {/*</div>*/}
                                    {/*<a href="!#" className="primary-btn">ADD TO CARD</a>*/}
                                    {/*<a href="!#" className="heart-icon"><span className="icon_heart_alt" /></a>*/}
                                    <ul>
                                        <li><b>Availability</b> <span>In Stock</span></li>
                                        <li><b>Shipping</b> <span>01 day shipping. <samp>Free pickup today</samp></span></li>
                                        <li><b>Weight</b> <span>0.5 kg</span></li>
                                        <li><b>Share on</b>
                                            <div className="share">
                                                <a href="#"><i className="fa fa-facebook" /></a>
                                                <a href="#"><i className="fa fa-twitter" /></a>
                                                <a href="#"><i className="fa fa-instagram" /></a>
                                                <a href="#"><i className="fa fa-pinterest" /></a>
                                            </div>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            <div className="col-lg-12">
                                <div className="product__details__tab">
                                    <ul className="nav nav-tabs" role="tablist">
                                        <li className="nav-item">
                                            <a className="nav-link active" data-toggle="tab" href="#tabs-1" role="tab" aria-selected="true">Description</a>
                                        </li>
                                    </ul>
                                    <div className="tab-content">
                                        <div className="tab-pane active" id="tabs-1" role="tabpanel">
                                            <div className="product__details__tab__desc">
                                                <div dangerouslySetInnerHTML={{ __html:dataDetail.content }} />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                {this.isOpenDataRelated()}
            </div>
        );
    }
}

export default Index;