import React, {Component} from 'react';
import ProductDetail from '../../components/Product/ProductDetail';
import {connect} from "react-redux";
import * as Actions from "../../actions";
import Loading from '../../components/Loading';
import Breadcrumb from '../../components/Breadcrumb';
import Helpers from '../../helpers';
import {Helmet} from 'react-helmet';
const loadJs = require('loadjs');

class Detail extends Component {
    componentDidMount() {
        loadJs('/js/main.js');
        let idSlug = this.props.match.params.id;
        const id = Helpers.splitIdDetail(idSlug);
        //console.log(this.props);
        this.props.fetchData(id);
    }

    showData = () => {
        if (this.props.data != null && this.props.fetching === false) {
            return (
                <div>
                    <ProductDetail data={this.props.data}/>
                </div>
            )
        } else {
            return <Loading />
        }
    }

    render() {
        return (
            <div>
                <Helmet>
                    <title>{this.props.data ? this.props.data.data.name : ''}</title>
                </Helmet>
                <Breadcrumb module='Sản phẩm' title={this.props.data ? this.props.data.data.name : ''}/>
                {this.showData()}
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        fetching: state.payloadProductDetail.fetching,
        id: state.payloadProductDetail.id,
        data: state.payloadProductDetail.data,
        error: state.payloadProductDetail.error
    };
};

const mapDispatchToProps = dispatch => {
    return {
        fetchData: (id) => dispatch(Actions.productDetailAction.requestProductDetailData(id))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Detail);